<template>
  <div class="game-intro">
    <header class="bg">
      <p class="subtitle">
        Use our website to get a recommendation of games based on your specific interests and preferences!
      </p>
    </header>
    <!-- <img src="https://m.media-amazon.com/images/I/71ccOb7sAyL.jpg" width="300" height="300"> -->
    <div class="filter"></div>
    <div class="game">
      <div class="title">
        <div class="side"></div>
        <div class="rightside">
          <div class="subject">Game</div>
          <!-- <div class="all" @click="getall">More</div> -->
          <div class="sl">
            <el-slider
              v-model="value"
              range
              show-stops
              :step="10"
              :max="150"
              @change="costchange"
            >
            </el-slider>
          </div>
          <div
            style="
              float: right;
              margin-top: 8px;
              margin-right: 20px;
              font-size: 20px;
            "
          >
            Cost Range (0-150):
          </div>
          <div class="category">
            <el-select v-model="select" slot="prepend" placeholder="Game Type">
              <el-option v-for="item, index in category" :key="index" :label="item.category" :value="index"></el-option>
            </el-select>
          </div>
        </div>
      </div>
      <div class="gamelist">
        <div class="slide" v-for="item in show" :key="item.game_id">
          <el-popover
            placement="top-start"
            width="300"
            trigger="hover"
            :content="item.Description"
          >
            <div class="image" slot="reference">
              <el-image
                :src="item['Image URL'].trim()"
                style="width: 100%; height: 100%"
              >
                <div slot="placeholder" class="image-slot">
                  loading<span class="dot">...</span>
                </div>
              </el-image>
            </div>
          </el-popover>
          <h2>{{ item.Name }}</h2>
          <p style="color: gray">{{ item.Category }}</p>
          <p style="color: white">${{ item["Cost in AUD"] }}</p>
          <div class="tags">
            <a :href="item['Shopping URL']" target="__blank">Get Game</a>
          </div>
          <div class="btn" @click="addtocart(item)">
            <div v-if="!$store.state.games[item.Name]">
              <el-tooltip
                class="item"
                effect="dark"
                content="Add to Plan"
                placement="top"
              >
                <el-button
                  type="warning"
                  icon="el-icon-star-off"
                  circle
                ></el-button>
              </el-tooltip>
            </div>
            <div v-else>
              <el-tooltip
                class="item"
                effect="light"
                content="Delete from Plan"
                placement="bottom"
              >
                <el-button
                  type="success"
                  icon="el-icon-check"
                  circle
                ></el-button>
              </el-tooltip>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { allgame, gamecategory } from "@/network/api.js";
import dv from "../../../components/exc/dv.vue";

let r;
export default {
  components: { dv },
  async created() {
    r = await allgame();
    this.show = r;
    // console.log(r);
    this.category = await gamecategory()
    // console.log(this.category)
  },
  methods: {
    addtocart(item) {
      this.$store.commit("modgame", item);
    },
    costchange() {
      if(this.select !== ""){
        this.show = r.filter(item => item.Category == this.category[Number(this.select)].category && item["Cost in AUD"] <= this.value[1] &&
          item["Cost in AUD"] >= this.value[0])
      } else {
        this.show = r.filter(
        (item) =>
          item["Cost in AUD"] <= this.value[1] &&
          item["Cost in AUD"] >= this.value[0]
      );
      }
    },
  },
  data() {
    return {
      show: [],
      value: [0, 150],
      select: "",
      category: [],
    };
  },
  watch: {
    select(newVal, oldVal) {
      // console.log(this.category[newVal].category)
      this.show = r.filter(item => item.Category == this.category[newVal].category && item["Cost in AUD"] <= this.value[1] &&
          item["Cost in AUD"] >= this.value[0])
    },
  }
};
</script>
<style scoped>
.game-intro {
  width: 100%;
}
.bg {
  width: 100%;
  height: 300px;
  background-image: url("../../../img/game.jpg");
  background-position-y: 400px;
  background-size: cover;
  box-shadow: gray 0 5px 5px 0;
  /* display: flex;
  justify-content: center;
  align-items: center; */
  position: relative;
}
.bg .subtitle {
  padding: 20px;
  border-radius: 15px;
  /* border: 1px solid #6c25a9; */
  position: absolute;
  left: 80px;
  top: 100px;
  width: 300px;
  color: #fff;
  font-size: 20px;
  font-family: Arial, Helvetica, sans-serif;
  background: rgba(0, 0, 0, 0.5);
}
.game .title {
  width: 90%;
  height: 40px;
  /* background-color: bisque; */
  display: flex;
  margin: auto;
  margin-top: 20px;
}
.game .title .side {
  width: 5px;
  height: 25px;
  background-color: coral;
  margin: 7px 10px 5px 10px;
  border-radius: 10px;
}
.title .rightside {
  width: 95%;
}
.title .subject {
  float: left;
  font-size: 22px;
  margin: 7px 0 0 10px;
  /* font-family: "Trebuchet MS", Arial, Helvetica, sans-serif; */
  font-family: Arial, Helvetica, sans-serif;
}
.title .sl {
  float: right;
  width: 40%;
}
.gamelist {
  margin: 0 auto;
  width: 90%;
  /* height: 350px; */
  display: flex;
  overflow: hidden;
  justify-content: center;
  flex-wrap: wrap;
}
.gamelist .slide {
  width: 210px;
  height: 330px;
  padding: 15px;
  margin: 20px;
  background: #000;
  /* cursor: pointer; */
  border-radius: 16px;
  position: relative;
}
.gamelist .image {
  margin: 0 auto;
  width: 190px;
  height: 190px;
  border-radius: 10px;
  overflow: hidden;
  position: relative;
}
.gamelist h2 {
  margin-top: 10px;
  color: white;
  font-size: 17px;
}

.gamelist p {
  margin-top: 10px;
}

.tags {
  margin: 0 0 2rem;
  padding: 0.5rem 0 1rem;
  line-height: 2;
  margin-bottom: 0;
}

.tags a {
  font-style: normal;
  font-weight: 700;
  color: #7a7a8c;
  text-transform: uppercase;
  font-size: 14px;
  border: 3px solid #28242f;
  border-radius: 2rem;
  padding: 0.2rem 0.85rem 0.25rem;
  position: relative;
  text-decoration: none;
  font-family: Arial, Helvetica, sans-serif;
}

.tags a:hover {
  background: linear-gradient(90deg, #ff8a00, #e52e71);
  text-shadow: none;
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
  -webkit-box-decoration-break: clone;
  background-clip: text;
  border-color: white;
}
.slide .btn {
  position: absolute;
  right: 10px;
  bottom: 10px;
}
.category {
  float: right;
  margin-right: 30px;
  width:150px
}
</style>